.gpt3__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;

    margin: 1rem;
}

.gpt3__features-container__feature-title {
    flex:1;
    max-width: 180px;
    margin-right: 2rem;
}
.gpt3__features-container__feature-title h1{

    font-family: var(--font-family);
    font-size: 24px;
    line-height: 30px;
    font-weight: 800;
    letter-spacing: -0.04em;
    margin-bottom: 1rem;
    color: #FFFFFF;

}
.gpt3__features-container__feature-title div{
    width: 38px;
    height: 3px;

    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.5rem;
}
.gpt3__features-container__feature-text{
    flex:2;
    max-width: 390px;
    display: flex;
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
}

@media screen and(max-width:550px){
.gpt3__features-container__feature-title h1 {
    font-size: 14px;
    line-height: 38px;
}
.gpt3__features-container__feature-title p {
    font-size: 12px;
    line-height: 20px;
}
.gpt3__features-container__feature{
    margin: 1rem 0;
}
}